.review-item{
  width: calc(100% - 36px);
  display: flex;
  align-items: center;
  background-color: white; 
  border-radius: 20px;
  box-shadow: 0px 1px 2px 1px rgba(119, 119, 119, 0.05), 0px 2px 4px 2px rgba(34, 34, 34, 0.05);
  padding: 18px;

  &__image{
    width: 32px;
    height: 32px;
    margin-right: 10px;
    background-color: grey;

    & > img{
      width: 32px;
      height: 32px;
    }
  }

  &__flex{
    flex-grow: 1;
  }

  &__information{
    display: flex;
    align-items: center;
  }

  &__nickname{
    font-size: 14px;
    font-weight: 600;
    color: #191F28;
  }

  &__date{
    margin: 0px 7px;
    font-size: 10px;
    font-weight: 400;
    color: #8B95A1;
  }

  &__content{
    margin-top: 3px;
    font-size: 13px;
    font-weight: 400;
    color: #191F28;
  }

  &__star > svg{
    width: 14px;
    height: 14px;
  }
}